import Vue from 'vue'
import VueResource from 'vue-resource'
import router from './router/router'
import publicJs from './public.js'
Vue.use(VueResource);
Vue.use(publicJs);

import App from './App.vue'


const app = new Vue({
  router,
  render: h => h(App),
}).$mount('#app');




